.auth-page {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #eef2ff;
}

.auth-card {
  width: 380px;
  padding: 35px;
  background: white;
  border-radius: 18px;
  box-shadow: 0 6px 28px rgba(0,0,0,0.1);
}

.auth-title {
  color: #1e1b4b;
  font-size: 24px;
  font-weight: bold;
}

.auth-subtitle {
  margin-bottom: 20px;
  color: #6b7280;
}

.input-group {
  margin-top: 15px;
}

.input-group label {
  font-size: 14px;
  color: #374151;
}

.input-group input {
  width: 100%;
  padding: 12px;
  border: 1.5px solid #c7d2fe;
  border-radius: 10px;
  margin-top: 6px;
  outline: none;
}

.btn-primary.full {
  width: 100%;
  margin-top: 20px;
  background: #4f46e5;
  color: white;
  padding: 12px;
  border-radius: 10px;
  border: none;
  font-size: 16px;
}

.switch-text {
  text-align: center;
  margin-top: 15px;
}