.dashboard-container {
  display: flex;
  height: 100vh;
  background: linear-gradient(-190deg,#e8eaee ,rgb(164, 165, 241));
  background-attachment: fixed;
  font-family: "Inter", sans-serif;
  overflow-x: hidden;

}

/* Sidebar */
.sidebar {
  width: 250px;
  background: #4f46e5;
  color: white;
  padding: 30px 20px;
  position: fixed;
  top: 0;
  right: -300px;
  height: 100%;
  z-index: 1000;
  box-shadow: 2px 0 12px rgba(0,0,0,0.2);
}


logo {
   color: white;
  font-size: 28px;
  font-weight: 700;
  margin-bottom: 40px;
}
h2{
   color:#4f46e5;
   font-weight: 500; 
}

.menu {
  list-style: none;
  padding: 0;
}

.menu li {
  margin: 18px 0;
  cursor: pointer;
  font-size: 16px;
  opacity: 0.9;
}

.menu li.active,
.menu li:hover {
  opacity: 1;
  font-weight: bold;
}

/* Overlay */
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  backdrop-filter: blur(4px);
  background: rgba(0,0,0,0.3);
  z-index: 500;
}



/*Main Section */
.main-section {
  flex: 1;
  padding: 25px 40px;
  width: 100%;
}

/* Topbar */
.topbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
  width: 100%;
}

.hamburger {
    width: 20px; height: 20px;
  font-size: 26px;
  cursor: pointer;
  z-index: 1200;
  display: flex;
  justify-content: center; align-items: center;
  border-radius: 12px;
  padding: 10px;
}


/* Hover effect */
.hamburger:hover {
  background: rgba(255,255,255,0.3);
  transform: scale(1.1);
}
/* ===========================
      UPGRADED PREPHUB HEADING
=========================== */

.prephub-heading {
  font-size: 33px;
  font-weight: 800;
  letter-spacing: 1px;
  margin: 0;
  cursor: default;

  /* Gradient Text */
  background: linear-gradient(90deg, #6A5ACD, #00A3FF);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

  /* Subtle shadow for depth */
  text-shadow: 0 2px 6px rgba(0,0,0,0.15);

  /* Smooth fade-in animation */
  opacity: 0;
  animation: fadeIn 0.8s forwards 0.3s;
}

.prephub-heading span {
  font-weight: 900;
  color: inherit;
}

/* Keyframes for fade-in */
@keyframes fadeIn {
  to {
    opacity: 1;
  }
}








.top-title {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  font-size: 24px;
  font-weight: 700;
}


.hero-text-section {
  margin: 30px 0;
  text-align: center;
}

.static-line {
  font-size: 32px;
  font-weight: 800;
  letter-spacing: 1px;
  color: #333;
  text-align: center;

  /*background: linear-gradient(90deg, #6A5ACD, #00A3FF);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;*/

  margin-bottom: 8px;
}

.dynamic-line {
  font-size: 28px;
  font-family: sans-serif;
  text-align:center;
  font-weight: 800;
  color: #604dd6;
  height: 36px; /* fixes layout jump */
  transition: all 0.3s ease;
}

