/* Center the Browse Companies section */
.browse-section {
  width: 90%;                 /* responsive width */
  max-width: 600px;           /* limit the size */
  margin: 2rem auto;          /* center horizontally */
  padding: 1.5rem;
  background: rgba(255,255,255,0.15);
  backdrop-filter: blur(8px);
  border-radius: 18px;
  text-align: center;         /* title centered */
  box-shadow: 0 4px 20px rgba(0,0,0,0.1);
}
.browse-section h3{
font-size:22px;
    font-weight: 700;
  margin-bottom: 12px;
  text-align: center;
}
/* Search bar inside the section */
.search-bar {
  display: flex;
  width: 100%;
  margin-top: 1rem;
  gap: 0.5rem;
}

/* Input should shrink properly on mobile */
.search-bar input {
  flex: 1;
  padding: 0.7rem 1rem;
  border-radius: 10px;
  border: none;
  outline: none;
  min-width: 0;  /* prevents overflow */
}

/* Search button */
.search-bar button {
  padding: 0.7rem 1.2rem;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  background: #4A6CFF;
  color: white;
  font-weight: bold;
  white-space: nowrap;
}

/* Suggestions (dropdown) */
.suggestions {
  margin-top: 0.7rem;
  background: rgba(255,255,255,0.25);
  border-radius: 10px;
  overflow: hidden;
}

      
/* Add Experience Button */
.add-experience {
  margin: 25px 0;
  text-align: center;
}

.btn-large {
  background: #4f46e5;
  color: white;
  border: none;
  padding: 14px 24px;
  border-radius: 10px;
  cursor: pointer;
  font-size: 16px;
}

/* Recently Added Cards */
.recently-added h3{
    font-size:22px;
    font-weight: 700;
  margin-bottom: 12px;
  text-align: center;

}
.cards-area {
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

.card {
  background: white;
  padding: 20px;
  border-radius: 18px;
  box-shadow: 0 6px 20px rgba(0,0,0,0.08);
  width: 250px;
  transition: transform 0.2s, box-shadow 0.2s;
}

.card h4 {
  color: #1e1b4b;
  font-size: 18px;
  margin-bottom: 10px;
}

.card p {
  margin: 6px 0;
  color: #6b7280;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .main-section {
    padding: 20px;
  }

  .search-bar {
    flex-direction: column;
  }

  .search-bar button {
    width: 100%;
  }

  .cards-area {
    justify-content: center;
  }
}

@media (max-width: 640px) {
  .top-title {
    font-size: 20px;
  }

  .btn-large {
    width: 100%;
  }

  .cards-area {
    flex-direction: column;
    align-items: center;
  }
}


  
/* Floating Settings Icon */
.floating-settings {
  position: fixed;
  bottom: 25px;
  right: 25px;
  background: rgba(255,255,255,0.15);
  backdrop-filter: blur(8px);
  border-radius: 50%;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  box-shadow: 0 8px 15px rgba(0,0,0,0.2);
  transition: background 0.3s, transform 0.2s, color 0.3s;
  z-index: 999;
  color: #fff;
}

.floating-settings:hover {
  transform: rotate(20deg) scale(1.1);
  background: rgba(255,255,255,0.25);
}

.floating-settings.active {
  background: linear-gradient(135deg, #6a11cb, #2575fc);
  color: #fff;
  transform: rotate(0deg) scale(1.1);
}

